import { Component } from '@angular/core';
@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.scss']
})
export class MainComponent {
  title = 'main';
  itemList=[{
    
  }];
  active={};
  ngOnInit(){
    console.log('************Desc:simpler editor****************')
    console.log('************Author:zhangbo****************')
    console.log('************E-mail:zhangb@geovis.com.cn***************')
    const $=(<any>window).$;
    $.get((window as any).SERVICE_URL).then(res=>{
      this.itemList=res;
    })
  }
  itemClick(item){
    this.active={};
    this.active[item.value]=true;
    this.goAnchor(item.value)
  }
  goAnchor(selector) {
    const anchor = document.querySelector("#anchor-" + selector);
    document.getElementById("examples-list").scrollTop =(anchor as any).offsetTop - 80;
  }
  openDeom(v){
    window.open('preview/'+v.value)
  }
}
